.cap-data-heading {
    p{
        font-size: 18px !important;
    }
    :nth-child(3){
        width: 30% !important;
    }
    :nth-child(2){
        width:50% !important;
    }
    

}
.cap-container{
    position: relative;
    .cap-loader{
        top: 50%;
    left: 45%;
    margin: 0;
    transform: translate(-50%, -50%);
    }
}

.cap-data-container{
    margin-top: -7px !important;
    gap: 3px !important;
    .cap-data{
        &.orange-data{
            >p{
                color: #f77f00 !important;
                font-size: 19px !important;
                >svg{
                    fill:#f77f00 !important;
                }
            }
            
        }
        &.purple-data{
            >p{
                color: #a06cd5 !important;
                font-size: 19px !important;
                >svg{
                    fill:#a06cd5 !important;
                }
            }
            
        }
        grid-template-columns:20% 50% 30% !important;
        p{
            font-size: 17px !important;
        }
       
        .team-name{
            padding: 6px 0px !important;
            justify-content: center !important;
        }
        :nth-child(3){
            font-size: 15px !important;
        }
    }
}
.cap-heading-container{
    &.o{
        div{
        border:  2px solid #f77f00;
        >h2{
            color: #f77f00 !important;
            >svg{
                fill: #f77f00;
            }
        }
        >button>svg{
            stroke: #f77f00;
        }
    }
}
&.p{
    div{
    border:  2px solid #a06cd5;
    >h2{
        color: #a06cd5 !important;
        >svg{
            fill: #a06cd5;
        }
    }
    >button>svg{
        stroke: #a06cd5;
    }
}
}
   >div{
    background-color: #3e5a65 !important;

    

    >h2{
        font-size: 28px !important;
        display: flex !important;
        align-items: center !important;
        gap: 8px;
        >svg{
            width: 45px !important;
            height: 45px !important;
        }
    }
    >button>svg{
        width: 32px !important;
    height: 32px !important;
    }
   }
}
.note-cover{
    padding: 5px 5px 10px;
    b{
        font-size: 15px;
    }
}

@media screen and (max-width:800px) {
    .cap-data-heading {
        p{
            font-size: 16px !important;
        }
        
    
    }
    .cap-container{
        position: relative;
        .cap-loader{
            top: 50%;
        left: 45%;
        margin: 0;
        transform: translate(-50%, -50%);
        }
    }
    
    .cap-data-container{
        margin-top: -4px !important;
        gap: 3px !important;
        .cap-data{
            &.orange-data{
                >p{
                    color: #f77f00 !important;
                    font-size: 15px !important;
                    >svg{
                        width: 28px;
                        height: 28px;
                    }
                }
                
            }
            &.purple-data{
                >p{
                    color: #a06cd5 !important;
                    font-size: 15px !important;
                    >svg{
                        width: 28px;
                        height: 28px;
                    }
                }
                
            }
                
            grid-template-columns:20% 50% 30% !important;
            p{
                font-size: 14px !important;
            }
           
            .team-name{
                padding: 4px 0px !important;
            }
            :nth-child(3){
                font-size: 13px !important;
            }
        }
    }
    .cap-heading-container{
            div{
                gap: 3px;
            >h2{
                font-size: 22px !important;
                gap: 4px;
                >svg{
                    width: 35px !important;
                    height: 35px !important;
                }
            }
        }
       >div{
    
        >button>svg{
            width: 20px !important;
        height: 20px !important;
        }
       }
    }
    .note-cover{
        padding: 0px 4px 7px;
        p{
            font-size: 9px !important;
        }
        
        b{
            font-size: 11px;
        }
    }
}