.profile{
    width: 90%;
    table{
        width: 90%;
        border-spacing:6px;
        background-color: #264653bd; 
        border-radius: 5px;
        .table-img{
            td{
                width: 35%;
                padding: 4px;
                background-color: #f4a361;
                border-radius: 6px;
            }
         

            img{
                border-radius: 6px;
                width: 100%;
            }
            
        }
        .table-name{
            background-color: #f4a361;
            border-radius: 5px;
            font-size: 20px;
        }
        td{
            border-radius: 3px;
            background-color: #f4a361;
            
        }
    }
 
     
}
.note-container{

    display: flex;
    margin-top: 10px;
    align-items: center;
    
    justify-content: center;
    transition: all 0.3s;

    .note-1{
        height: 40px;
        font-size: 17px;
        height: 90%;
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #f4a361b8;
        padding: 3px;
        border-radius: 4px;
        border:solid 3px #264653d2;
    }
    
    .note-2{
        font-size: 12px;
        background-color: #f4a361;
        transition: all 0.3s;
        border-radius:0px 4px 4px 0px;
        >.arrow{
            font-size: 16px;
            display:'inline-flex';
            font-weight: 1000;
            line-height:0;
            scale:1.2
          }
    }

}
.center{
    top:50%;
    transform: translateY(-50%);
}
@media screen and (max-width:800px){
    .note-container{

        
        .note-1{
            height: 30px;
            font-size: 14px;
            padding: 2px;
            border-radius: 4px;
            border:solid 3px #264653d2;
        }
        
        .note-2{
           font-weight:lighter;
            font-size: 9px;
            padding: 1px;
            border-radius:0px 4px 4px 0px;
            >.arrow{
                font-size: 11px;
              }

        }

}
.remove-margin-note-container{
    margin: 10px auto 0px ;
}
    .profile{
        width: 100%;
        table{
            width: 90%;
            margin: 0px 0px 0px 30px;
            border-spacing:3px;
            .table-img{
                td{
                
                    padding: 2px;
                }
                
            }
            td{
               font-size: 12px;
            }
        }
        
}
}
