.tournament-container{
    max-width: 95%;
    height: 75vh;
    background-color:#264653 ; 
    padding: 10px; 
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: flex-start;
    gap: 10px;
    >div{
        margin: 0;
    }
    .Leaderboard-data-container{
        position: relative;
        margin-top: -5px;
        
    }
    .selection-div{
        padding: 5px; 
        >select{
            margin: 0 !important;

        }
    }
    .loader{
        top: 22%;
    left: 45%;
        margin: 0;
        transform: translate(-50%, -50%);
    }
    .tournament-heading-container-1{
        display: flex;
        background-color: #f4a361;
        justify-content: space-between;
        gap:5px;
        padding: 5px;
        border-radius: 9px;
        >h2>svg{
            width: 50px;
            height: 50px;
        }
        button{
            position: static;

        }
       
    }
    P{
        font-size: 18px;
    }
    .country-cup-data-heading{
        width: 100%;
        display: flex;
        p{
           font-size: 20px;
            background-color:#f4a361 ;  
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 2px;
            border: 1px solid #264653;
            height: 100%;
        }
        p:nth-child(odd){
            width: 20%;
        }
        p:nth-child(2){
            width: 60%;
       
        }
        p:nth-child(3){
            flex-direction: column;
            font-size: 16px;
            >span{
                font-size: 12px;
            }
          
        }
        
    }
    .country-cup-data{
        display: grid;
        width: 100%;
        border-radius: 5px;
        background-color: #f4a361;
        grid-template-columns: 20% 60% 20%;
        grid-template-rows: fit-content;
        background-color:#d9d9d922 ;
        p{
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
            border: 1px solid #264653;
            font-size: 18px;
            color: #c9cba3;
        }
        p:nth-child(3){
            flex-direction: column;
            font-size: 16px;
            >span{
                color: #c9cba3;
                font-size: 12px;
            }
          
        }
        .team-name{
            font-family: 'Montserrat', sans-serif;
            padding: 12px 0px;
            text-align: center;
            display: flex;
            justify-content: flex-start;
            gap: 15px;
            font-size: 16px;
            align-items: center;
            img{
                display: inline-block;
                scale: 1.4;
                transform-origin: center;
                margin-left: 15px;
            }
        }
    }
    .overall-stats-data{
        grid-template-columns: 70% 20%;
        height: 50px;
        >p{
            font-family: 'Montserrat', sans-serif;
            border: none;
            >svg{
                fill:#f4a361;
                margin: 0;
                height: 40px;
                .st30day0{
                    fill:#f4a361;
                }
            }
        }
    }
    img{
        width: 24px;
        margin: 0;
    }
    .country-cup-radio-button{
        display: flex;
        justify-content: space-around;
        >div{
            input{
                display: none
            }
            input[type="radio"]:checked+label{
                background-color: #264653;
                color: #f4a361;
                border:1px solid #f4a361;
                svg{
                    fill: #f4a361;
                    stroke: #f4a361;
                }
            }
        }
        >div>label{
            display: flex;
            justify-content: center;
            padding: 3px;
            gap: 5px;
            align-items: center;
            background: none;
            border: none;
            background-color: #f4a361;
            border-radius: 5px;
            border:1px solid #264653;
            transition: all 0.3s;
    
             
        }
    }
    .tournament-middle-container-1, .tournament-middle-container-2{
        display: flex;
        margin-top: 5px;
        gap:10px
    }
    .tournament-middle-container-2{
        margin-top: 0px;
    }
    .play-off-info{
        background-color: #f4a361;
        width: fit-content;
        padding: 5px; 
        border-radius: 5px;
    }
}
.qualified{
    color: #dbb42c !important;
}
.eliminated{
    color: #da7179 !important; 
}

@media screen and (max-width:800px){

    .tournament-container{
        padding: 5px; 
        gap: 7px;
        .tournament-middle-container-1, .tournament-middle-container-2{
            margin-top: 3px;
            gap:5px
        }
        .selection-div{
            >select{ 
            &#contribute{
               font-size: 12px !important;
    
            }
        }
        }
        .loader{
            top: 22%;
        left: 45%;
            margin: 0;
            transform: translate(-50%, -50%);
        }
      
        .tournament-heading-container-1{
            padding: 3px;
    
            >h2{
                font-size: 25px;
            }
            >h2>svg{
               
                width: 40px;
                height: 40px;
            }
           
        }
        P{
            font-size: 14px;
        }
        .country-cup-data-heading{

            p{
               font-size: 16px;
                padding: 1px;
            }
            p:nth-child(3){
                font-size: 14px;
                >span{
                    font-size: 11px;
                }
              
            }
            
        }
        .country-cup-data{
            p{
            
                font-size: 13px;
            }
            p:nth-child(3){
                font-size: 13px;
                >span{
                    font-size: 9px;
                }
              
            }
            .team-name{
                padding: 7px 0px;
                gap: 5px;
                font-size: 10px;
               
                img{
                    width: 20px;
                    scale: 1.1;
                    margin-left: 5px;
                }
            }
        }
        .overall-stats-data{
            height: 35px;
            >p{
                >svg{
                    height: 30px;
                }
            }
        }
        img{
            width: 24px;
        }
        .country-cup-radio-button{
            >div>label{
                padding: 3px;
                gap: 5px;
                font-size: 14px;
                >svg{
                    width: 24px;
                    height: 24px;
                }
            }
        }
        .play-off-info{
            padding: 2px;
            margin-top: 7px;
            >p{
                font-size: 12px !important;
            }
        }
    }

}




.button-54 {
    font-family: "Open Sans", sans-serif;
    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;
    color:#f77f00;
    cursor: pointer;
    border: 3px solid;
    padding: 0.25em 0.5em;
    box-shadow: 1px 1px 0px 0px, 2px 2px 0px 0px, 3px 3px 0px 0px, 4px 4px 0px 0px, 5px 5px 0px 0px;
    position: relative;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    background-color: #3e5a65;
    border-radius: 5px;
  
    >span{
        color: #f77f00;
        font-size: 12px;
        >svg{
            fill: #f77f00;
            width: 35px;
            height: 35px;
        }
    }
    &.p{
        color: #a06cd5;
        >span{
            color: #a06cd5;
            font-size: 12px;
            >svg{
                fill: #a06cd5;
                width: 35px;
                height: 35px;
            }
        }
    
      }

  }
  .button-54:not([disabled]):active{
    box-shadow: 0px 0px 0px 0px;
    top: 5px;
    left: 5px;
  }
  
  @media  screen  and (max-width:800px)  {

    .button-54 {
        letter-spacing: 1px;
        border: 2px solid;
        padding: 3px 3px;
        border-radius: 5px;
      
        >span{
            font-size: 8px !important;
            margin: 0px !important;
            >svg{
                width: 25px !important;
                height: 25px !important;
            }
        }
    
      }
  }